<template>
  <div class="gain-content">
    <div class="header-content">
      <pageTitle :title="pageName"></pageTitle>
      <switch-btn-list
        :btnList="switchList"
        @changeType="changeAllType"
      ></switch-btn-list>
    </div>
    <div
      v-if="showZgs"
      class="main-content"
    >
      <div class="block-content-table">
        <min-title
          :nameVal="mlfxzgsTitle"
          :show="'1'"
          :type="'gain-zgs-mlfx'"
          :tabsData="blockSwitchList"
          @tabsType="tabsType"
        ></min-title>
        <table-list
          :tableData="mlfxzgsTableData"
          :type="'gain-mlfx'"
          @changeTableOrder="changeTableOrder"
          :needClick="false"
          :defaultSelected="true"
          @changeTableRow="changeTableRow"
        ></table-list>
      </div>
      <div class="block-content-chart">
        <min-title
          :nameVal="qsfxTitle"
          :show="'3'"
        ></min-title>
        <div class="block-chart">
          <BarLineChart
            :chart-data="qsfxLineData"
            :option="qsfxLineOption(qsfxLineData)"
          ></BarLineChart>
        </div>
      </div>
      <div class="block-content-chart">
        <min-title
          :nameVal="ystbfxTitle"
          :tabsData="blockSwitchList"
          :show="'1'"
          :type="'gain-zgs-ystbfx'"
          @tabsType="tabsType"
        ></min-title>
        <div class="block-chart">
          <BarLineChart
            :chart-data="zgsYstfxBarLineData"
            :option="zgsYstbfxBarLineOption(zgsYstfxBarLineData)"
          ></BarLineChart>
        </div>
      </div>
      <div class="block-content-chart">
        <min-title
          :nameVal="ysqsfxTitle"
          :tabsData="blockSwitchList_Ysfx"
          :show="'1'"
          :type="'gain-ysqsfx'"
          @tabsType="tabsType"
        ></min-title>
        <div class="block-chart">
          <BarLineChart
            :chart-data="ysqsfxLineData"
            :option="ysqsfxLineOption(ysqsfxLineData)"
          ></BarLineChart>
        </div>
      </div>
    </div>
    <div
      v-if="!showZgs"
      class="main-content"
    >
      <div class="block-content-table">
        <min-title
          :nameVal="mlgldpTitle"
          :show="'3'"
          :type="'gain-dp-glfx'"
          @tabsType="tabsType"
        ></min-title>
        <table-list
          :tableData="mlgldpTableData"
          :type="'gain-pmfx'"
        ></table-list>
      </div>
      <div class="block-content-table">
        <min-title
          :nameVal="mlfxdpTitle"
          :show="'1'"
          :type="'gain-dp-mlfx'"
          :tabsData="blockSwitchList"
          @tabsType="tabsType"
        ></min-title>
        <table-list
          :tableData="mlfxdpTableData"
          :type="'gain-mlfxdp'"
          @changeTableOrder="changeTableOrder"
          :needClick="true"
          @changeTableRow="changeTableRow"
        ></table-list>
      </div>
      <div class="block-content-chart">
        <min-title
          :nameVal="qsfxdpTitle"
          :show="'3'"
        ></min-title>
        <div class="block-chart">
          <BarLineChart
            :chart-data="qsfxdpLineData"
            :option="qsfxdpLineOption(qsfxdpLineData)"
          ></BarLineChart>
        </div>
      </div>
      <div class="block-content-chart">
        <min-title
          :nameVal="ystbfxdpTitle"
          :tabsData="blockSwitchList"
          :show="'1'"
          :type="'gain-dp-ystbfx'"
          @tabsType="tabsType"
        ></min-title>
        <div class="block-chart">
          <BarLineChart
            :chart-data="dpBarLineData"
            :option="dpBarLineOption(dpBarLineData)"
          ></BarLineChart>
        </div>
      </div>
      <div class="block-content-chart">
        <min-title
          :nameVal="ysqsfxdpTitle"
          :tabsData="blockSwitchList_Ysfx"
          :show="'1'"
          :type="'gain-dp-ysqsfx'"
          @tabsType="tabsType"
        ></min-title>
        <div class="block-chart">
          <BarLineChart
            :chart-data="ysqsfxdpLineData"
            :option="ysqsfxdpLineOption(ysqsfxdpLineData)"
          ></BarLineChart>
        </div>
      </div>
      <div class="block-content-table">
        <min-title
          :nameVal="jgfxdpTitle"
          :show="'3'"
        ></min-title>
        <type-list
          :tabsData="jgfxdpTypeList"
          :switchType="'gain-jgfx-type'"
          @activeTypeName=changeJgdpfxType
        ></type-list>
        <div class="switch-btn">
          <div class="switch-btn-core">
            <tabs :tabsData="blockSwitchList" @tabsType="tabsType" :switchType="'gain-jgfx-up'"></tabs>
          </div>
        </div>
        <table-list
          :tableData="jgfxdpTableData"
          :type="'gain-dp-jgfx'"
          @changeTableOrder="changeTableOrder"
        ></table-list>
      </div>
    </div>
  </div>
</template>

<script>
import pageTitle from '../../components/common/pageTitle'
import MinTitle from '../../components/common/minTitle'
import TableList from '../../components/common/tableList'
import Tabs from '../../components/common/tabs'
import TypeList from '../../components/common/typeList'
import BarLineChart from '../../components/echarts/BarLineChart'
import SwitchBtnList from '../../components/common/switchBtnList'
import _ from 'lodash'
import { px2px } from '../../assets/js/lib/common/flexible' // 在echarts的option配置中，用到的字体大小，柱图宽度等数字时，要用此方法包起来
import {initNum, compareSort, formatNumber, switchPercent} from '../../assets/js/core/weapon' // 引入排序方法
import apiService from '../../assets/js/api'
export default {
  name: '',
  data () {
    return {
      pageName: '毛利(万元)', // 页面的名字
      switchList: [{ name: '子公司' }, { name: '店铺' }], // 上部的切换按钮
      showZgs: true, // 直营和子公司进行切换展示
      mlfxzgsTitle: '毛利分析-子公司',
      mlfxZgsTableDataInit: [],
      qsfxBarLineDataInit: [],
      ystbfxBarLineDataInit: [],
      zgsYstfxBarLineData: [],
      ysqsfxLineDataInit: [],
      mlglDpTableDataInit: [],
      mlfxDpTableDataInit: [],
      qsfxDpLineDataInit: [],
      ystbfxDpBarLineDataInit: [],
      ysqsfxDpLineDataInit: [],
      qsfxTitle: '趋势分析',
      ystbfxTitle: '因素同比分析',
      ysqsfxTitle: '因素趋势分析',
      mlfxdpTitle: '毛利分析-店铺',
      mlgldpTitle: '毛利概览-子公司',
      qsfxdpTitle: '趋势分析',
      ystbfxdpTitle: '因素同比分析',
      ysqsfxdpTitle: '因素趋势分析',
      jgfxdpTitle: '结构分析',
      jgfxdpIndex: 0,
      jgfxdpTypeIndex: 0,
      mlfxZgsIndex: 0,
      mlfxDpIndex: 0,
      ystbfxIndex: 0,
      ystbfxDpIndex: 0,
      ysqsfxZgsTypeIndex: 0,
      ysqsfxDpTypeIndex: 0,
      jgfxdpTypeList: [{name: '大类', value: '大类'}, {name: 'SPU', value: 'SPU'}],
      jgfxdpTableData: {
        tableHeader: [
          {name: '大类', prop: 'dl', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '收入同比', prop: 'srtb', needOrder: true, order: '2', isPresent: true, needArrow: false, newLine: true},
          {name: '成本同比', prop: 'cbtb', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: true},
          {name: '毛利率', prop: 'mll', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
          {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: true, needArrow: false},
          {name: '差异', prop: 'cy', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
        ],
        tableContent: []
      },
      blockSwitchList: [
        { name: '月', value: 'M' },
        { name: '季', value: 'J' },
        { name: '年累', value: 'NL' }
      ], // 每个小块的时间切换按钮
      blockSwitchList_Ysfx: [{name: '收入', value: '收入'}, {name: '成本', value: '成本'}],
      dpBarLineData: [],
      ysqsfxLineData: [],
      ysqsfxdpLineData: [],
      qsfxLineData: [],
      qsfxdpLineData: [],
      mlgldpTableData: {
        tableHeader: [
          {
            name: '时间',
            prop: 'sj',
            needOrder: false,
            order: '0',
            isPresent: false,
            needArrow: false
          },
          {
            name: '收入',
            prop: 'sr',
            needOrder: false,
            order: '1',
            isPresent: false,
            needArrow: false
          },
          {
            name: '毛利',
            prop: 'ml',
            needOrder: false,
            order: '0',
            isPresent: false,
            needArrow: false
          },
          {
            name: '毛利率',
            prop: 'mll',
            needOrder: false,
            order: '0',
            isPresent: true,
            needArrow: false
          },
          {
            name: '差异',
            prop: 'tq',
            needOrder: false,
            order: '0',
            isPresent: true,
            needArrow: true
          }
        ],
        tableContent: []
      },
      mlfxzgsTableData: {
        tableHeader: [
          {
            name: '子公司',
            prop: 'zgs',
            needOrder: false,
            order: '0',
            isPresent: false,
            needArrow: false
          },
          {
            name: '毛利率',
            prop: 'mll',
            needOrder: true,
            order: '2',
            isPresent: true,
            needArrow: false,
            newLine: true
          },
          {
            name: '毛利率排名',
            prop: 'mllpm',
            needOrder: true,
            order: '0',
            isPresent: false,
            needArrow: false,
            newLine: true
          },
          {
            name: '差异',
            prop: 'cy',
            needOrder: true,
            order: '0',
            isPresent: true,
            needArrow: false,
            newLine: true
          },
          {
            name: '差异排名',
            prop: 'cypm',
            needOrder: true,
            order: '0',
            isPresent: false,
            needArrow: true,
            newLine: true
          }
        ],
        tableContent: []
      },
      mlfxdpTableData: {
        tableHeader: [
          {name: '店铺', prop: 'dp', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '收入', prop: 'sr', needOrder: true, order: '2', isPresent: false, needArrow: false},
          {name: '毛利', prop: 'ml', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '毛利率', prop: 'mll', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
          {name: '同期毛利率', prop: 'tqmll', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: true},
          {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
        ],
        tableContent: []
      }
    }
  },
  computed: {
    dateStr () {
      return this.$store.state.dateStr
    },
    dateStrM () {
      return this.$store.getters.dateStrM
    },
    dateStrJ () {
      return this.$store.getters.dateStrJ
    },
    selectedDwName () {
      return this.$store.state.selectedDwName
    }
  },
  components: {
    pageTitle,
    MinTitle,
    TableList,
    Tabs,
    SwitchBtnList,
    BarLineChart,
    TypeList
  },
  created () {
    this.$store.commit('updateHeaderTitle', '扣前毛利')
    this.$store.commit('updateShowIcon', true)
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    if (this.dateStr !== '') {
      this.getAllData()
    }
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '子公司', URL: '毛利'})
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  },
  /**
  watch: {
    dateStr: {
      handler () {
        this.getAllData()
      }
    },
    selectedDwName: {
      handler () {
        this.qsfxTitle = '趋势分析-' + this.selectedDwName
        this.ystbfxTitle = '因素同比分析-' + this.selectedDwName
        this.ysqsfxTitle = '因素趋势分析-' + this.selectedDwName
        this.getQsfxBarLineData(this.selectedDwName)
        this.getYstbfxBarLineData(this.selectedDwName)
        this.getYsqsfxBarLineData(this.selectedDwName)
        this.getMlfxDpTableData(this.selectedDwName)
      }
    }
  },
   **/
  methods: {
    getAllData () {
      this.getZgsMlfxTableData()
      this.qsfxTitle = '趋势分析-' + this.selectedDwName
      this.ystbfxTitle = '因素同比分析-' + this.selectedDwName
      this.ysqsfxTitle = '因素趋势分析-' + this.selectedDwName
      this.getQsfxBarLineData(this.selectedDwName)
      this.getYstbfxBarLineData(this.selectedDwName)
      this.getYsqsfxBarLineData(this.selectedDwName)
      this.getMlfxDpTableData(this.selectedDwName)
      this.getMlglDpTableData(this.selectedDwName)
    },
    tabsType ({ activeValue, name, type, index }) {
      if (type === 'gain-zgs-mlfx') {
        this.mlfxZgsIndex = index
        this.dealMlfxZgsTableData()
      }
      if (type === 'gain-zgs-ystbfx') {
        this.ystbfxIndex = index
        this.dealYstbfxBarLineData()
      }
      if (type === 'gain-ysqsfx') {
        this.ysqsfxZgsTypeIndex = index
        this.dealYsqsfxBarLineData()
      }
      if (type === 'gain-dp-ysqsfx') {
        this.ysqsfxDpTypeIndex = index
        this.dealDpYsqsfxBarLineData()
      }
      if (type === 'gain-dp-mlfx') {
        this.mlfxDpIndex = index
        this.dealMlfxDpTableData()
      }
      if (type === 'gain-dp-ystbfx') {
        this.ystbfxDpIndex = index
        this.dealDpYstbfxBarLineData()
      }
      if (type === 'gain-jgfx-up') {
        this.jgfxdpIndex = index
        this.dealJgfxDpTableData()
      }
    },
    changeAllType ({ btn, index }) {
      if (index === 0) {
        this.showZgs = true
        this.mlfxZgsIndex = 0
        this.ystbfxIndex = 0
        this.ysqsfxZgsTypeIndex = 0
        this.dealMlfxZgsTableData()
        this.dealYstbfxBarLineData()
        this.dealYsqsfxBarLineData()
      } else {
        this.showZgs = false
        this.jgfxdpIndex = 0
        this.mlfxDpIndex = 0
        this.ystbfxDpIndex = 0
        this.jgfxdpTypeIndex = 0
        this.dealMlglDpTableData()
        this.dealDpYstbfxBarLineData()
        this.dealYsqsfxBarLineData()
      }
    },
    changeJgdpfxType ({ activeValue, name, type, index }) {
      if (type === 'gain-jgfx-type') {
        this.jgfxdpTypeIndex = index
        this.dealJgfxDpTableData()
      }
    },
    changeTableRow ({row, name, type}) {
      // 当点击了表格的某一行，进行联动操作
      // this.mlfxzgsTitle = '毛利分析-' + name
      if (type === 'gain-mlfx') {
        this.qsfxTitle = '趋势分析-' + name
        this.getQsfxBarLineData(name)
        this.ystbfxTitle = '因素同比分析-' + name
        this.getYstbfxBarLineData(name)
        this.ysqsfxTitle = '因素趋势分析-' + name
        this.getYsqsfxBarLineData(name)
        this.getMlfxDpTableData(name)
      } else if (type === 'gain-mlfxdp') {
        this.qsfxdpTitle = '趋势分析-' + name
        this.getDpQsfxBarLineData(name)
        this.ystbfxdpTitle = '因素同比分析-' + name
        this.getDpYstbfxBarLineData(name)
        this.ysqsfxdpTitle = '因素趋势分析-' + name
        this.getDpYsqsfxBarLineData(name)
        this.jgfxdpTitle = '结构分析-' + name
        this.getJgfxDpTableData(name)
      }
    },
    changeTableOrder ({ header, type }) {
      // debugger
      let tempTable = { tableHeader: [], tableContent: [] }
      if (type === 'gain-mlfx') {
        // 这个来分辨哪个表格需要排序
        tempTable = _.cloneDeep(this.mlfxzgsTableData)
      } else if (type === 'gain-mlfxdp') {
        tempTable = _.cloneDeep(this.mlfxdpTableData)
      } else if (type === 'gain-dp-jgfx') {
        tempTable = _.cloneDeep(this.jgfxdpTableData)
      }

      const headerList = []
      let tableContent = []
      for (let item of tempTable.tableHeader) {
        if (header.order === '0') {
          // 说明想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '1') {
          // 说明之前是升序，想要降序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '2'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '2') {
          // 说明之前是降序，想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        }
        headerList.push(item)
      }
      if (header.order === '0') {
        // 说明想要升序
        tableContent = tempTable.tableContent.sort(
          compareSort(header.prop, true)
        )
      } else if (header.order === '1') {
        // 说明之前是升序，想要降序
        tableContent = tempTable.tableContent.sort(
          compareSort(header.prop, false)
        )
      } else if (header.order === '2') {
        // 说明之前是降序，想要升序
        tableContent = tempTable.tableContent.sort(
          compareSort(header.prop, true)
        )
      }
      if (type === 'gain-mlfx') {
        // 这个来分辨哪个表格需要排序
        this.mlfxzgsTableData.tableHeader = headerList
        this.mlfxzgsTableData.tableContent = tableContent
      }
      if (type === 'gain-mlfxdp') {
        this.mlfxdpTableData.tableHeader = headerList
        this.mlfxdpTableData.tableContent = tableContent
      }
      if (type === 'gain-dp-jgfx') {
        this.jgfxdpTableData.tableHeader = headerList
        this.jgfxdpTableData.tableContent = tableContent
      }
    },
    zgsYstbfxBarLineOption (dataArr) {
      const labelList = []
      const valueList = []
      for (let item of this.zgsYstfxBarLineData) {
        labelList.push(item.label)
        valueList.push(item.value)
      }
      const option = {
        legend: {
          show: true,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        xAxis: [
          {
            type: 'category',
            name: '',
            interval: 0,
            nameLocation: 'start',
            nameTextStyle: {
              color: '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            data: [],
            axisPointer: {
              type: 'shadow'
            },
            axisLabel: {
              show: true,
              interval: 0,
              textStyle: {
                color: '#71768C'
              },
              fontSize: px2px(24)
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#98FB98'
              }
            },
            axisTick: {
              show: false
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '',
            nameTextStyle: {
              color: '#71768C',
              fontSize: px2px(24)
            },
            position: 'right',
            axisLabel: {
              show: false,
              fontSize: px2px(24),
              textStyle: {
                color: '#71768C'
              },
              formatter: '{value}%'
            },
            axisLine: {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: '#7d838b'
              }
            },
            axisTick: {
              show: false
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: '收入同比',
            smooth: true,
            barWidth: px2px(80),
            symbol: 'none',
            yAxisIndex: 0,
            label: {
              show: true,
              position: 'top',
              fontFamily: 'PingFangSC-Regular',
              color: '#71768C',
              fontSize: px2px(24),
              formatter: function (param) {
                // console.info(initNum(param.value) === 0)
                if (initNum(param.value) === 0) {
                  return ''
                } else {
                  return param.value + '%'
                }
              }
            },
            barGap: '-100%',
            itemStyle: {
              color: '#FFAB00'
            },
            data: []
          },
          {
            type: 'bar',
            name: '成本同比',
            smooth: true,
            label: {
              show: true,
              position: 'top',
              fontFamily: 'PingFangSC-Regular',
              color: '#71768C',
              fontSize: px2px(24),
              formatter: function (param) {
                if (initNum(param.value) === 0) {
                  return ''
                } else {
                  return param.value + '%'
                }
              }
            },
            barWidth: px2px(80),
            symbol: 'none',
            yAxisIndex: 0,
            itemStyle: {
              color: '#0000FF'
            },
            data: []
          },
          {
            type: 'bar',
            name: '毛利率差异',
            smooth: true,
            label: {
              show: true,
              position: 'top',
              fontFamily: 'PingFangSC-Regular',
              color: '#71768C',
              fontSize: px2px(24),
              formatter: function (param) {
                if (initNum(param.value) === 0) {
                  return ''
                } else {
                  return param.value + '%'
                }
              }
            },
            barWidth: px2px(80),
            symbol: 'none',
            yAxisIndex: 0,
            itemStyle: {
              color: '#87CEFA'
            },
            data: []
          }
        ]
      }

      option.xAxis[0].data = labelList
      option.series[0].data[0] = {value: valueList[0], itemStyle: {barBorderRadius: parseFloat(valueList[0]) > 0 ? [px2px(40), px2px(40), 0, 0] : [0, 0, px2px(40), px2px(40)]}}
