    // 毛利概览的数据
    async getZgsMlfxTableData () {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gainCompany.getZgsmlfxTableData, {dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.mlfxZgsTableDataInit = result
        this.dealMlfxZgsTableData()
      }
    },
    dealMlfxZgsTableData () {
      const list = []
      let dataList = []
      if (this.mlfxZgsIndex === 0) {
        dataList = this.mlfxZgsTableDataInit.resM || []
      } else if (this.mlfxZgsIndex === 1) {
        dataList = this.mlfxZgsTableDataInit.resJ || []
      } else if (this.mlfxZgsIndex === 2) {
        dataList = this.mlfxZgsTableDataInit.resNl || []
      }
      for (let item of dataList) {
        const obj = {}
        obj.zgs = item.distribution_name
        obj.mll = (initNum(item.margin_rate) * 100).toFixed(1)
        obj.mllpm = formatNumber(initNum(item.order_margin_rate).toFixed(0), 0)
        obj.cy = (initNum(item.deviation) * 100).toFixed(1)
        obj.cypm = formatNumber(initNum(item.order_deviation).toFixed(0), 0)
        list.push(obj)
      }
      this.mlfxzgsTableData = {
        // 这里是为了重置排序，以第一个排序列开始排序
        tableHeader: [
          {
            name: '子公司',
            prop: 'zgs',
            needOrder: false,
            order: '0',
            isPresent: false,
            needArrow: false
          },
          {
            name: '毛利率',
            prop: 'mll',
            needOrder: true,
            order: '2',
            isPresent: true,
            needArrow: false,
            newLine: false
          },
          {
            name: '毛利率排名',
            prop: 'mllpm',
            needOrder: true,
            order: '0',
            isPresent: false,
            needArrow: false,
            newLine: true
          },
          {
            name: '差异',
            prop: 'cy',
            needOrder: true,
            order: '0',
            isPresent: true,
            needArrow: false,
            newLine: true
          },
          {
            name: '差异排名',
            prop: 'cypm',
            needOrder: true,
            order: '0',
            isPresent: false,
            needArrow: true,
            newLine: true
          }
        ],
        tableContent: list.sort(compareSort('mll', false))
      }
      // if (this.mlfxzgsTableData.tableContent.length > 0) {
      //   this.qsfxTitle = '趋势分析-' + this.mlfxzgsTableData.tableContent[0].zgs
      //   this.ystbfxTitle = '因素同比分析-' + this.mlfxzgsTableData.tableContent[0].zgs
      //   this.ysqsfxTitle = '因素趋势分析-' + this.mlfxzgsTableData.tableContent[0].zgs
      //   this.$eventBus.$emit('resetSelectedRow', {type: 'gain-mlfx', name: this.mlfxzgsTableData.tableContent[0].zgs})
      //   this.getQsfxBarLineData(this.mlfxzgsTableData.tableContent[0].zgs)
      //   this.getYstbfxBarLineData(this.mlfxzgsTableData.tableContent[0].zgs)
