    async getYsqsfxBarLineData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gainCompany.getZgsYsqsfxBarLineData, {subName: companyName, dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.ysqsfxLineDataInit = result.res
        this.dealYsqsfxBarLineData()
      }
    },
    dealYsqsfxBarLineData () {
      const list = []
      let dataList = []
      dataList = this.ysqsfxLineDataInit || []
      for (let item of dataList) {
        const obj = {}
        obj.label = item.period_wid
        obj.tb = (initNum(item.target_yoy) * 100).toFixed(1)
        if (this.ysqsfxZgsTypeIndex === 0) {
          obj.sr = (initNum(item.income_amt)).toFixed(1)
          obj.srtb = (initNum(item.income_yoy) * 100).toFixed(1)
        } else if (this.ysqsfxZgsTypeIndex === 1) {
          obj.sr = (initNum(item.cost_amt)).toFixed(1)
          obj.srtb = (initNum(item.cost_yoy) * 100).toFixed(1)
        }

        list.push(obj)
      }
      this.ysqsfxLineData = list
    },
    async getDpYsqsfxBarLineData (storeName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gainCompany.getDpYsqsfxBarLineData, {cusName: storeName, dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
