    async getMlglDpTableData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gainCompany.getDpMlglTableData, {subName: companyName, dateStrM: this.dateStrM, dateStrD: this.dateStrD})
      if (Object.is(__statusCode, '1')) {
